.typing {
  width: 22ch;
  width: 100%;
  animation: typing 2s steps(22), blink .5s step-end infinite alternate;
  white-space: nowrap;
  overflow: hidden;
  border-right: 3px solid;
  font-family: monospace;
  font-size: 2em;

  &--mobile {
    display: none;
  }
}

@keyframes typing {
  from {
    width: 0;
  }
}

@keyframes blink {
  50% {
    border-color: transparent;
  }
}


@include vp-tablet {
  .typing {
    animation: none;
    border: none;
  }
}

@include vp-mobile {
.typing {
  &--desktop{
    display: none;
  }

  &--mobile {
    display: block;
  }
}
}
