@mixin set-hover() {
  &::after {
    content: " ";
    position: absolute;
    top: 0;
    left: 0;
    z-index: 5;
    width: 100%;
    height: 100%;
    background: $color-primary-black-opacity-089;
  }

  & .coaches__skills {
    display: flex;
  }
}

@mixin reset-ul() {
  margin: 0;
  padding: 0;

  list-style: none;
}

@mixin visually-hidden () {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;

  white-space: nowrap;

  border: 0;

  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

@mixin neon {
  color: $color-default-white;
  text-shadow:
    0 0 5px  $color-secondary,
    0 0 10px $color-secondary,
    0 0 15px $color-secondary,
    0 0 25px $color-secondary,
    0 0 35px $color-secondary,
    0 0 45px $color-secondary,
    0 0 55px $color-secondary,
    0 0 65px $color-secondary;
  animation: pink_lights 20s infinite;

  @keyframes pink_lights {
    20% {
      text-shadow: 0 0 20px $color-secondary-highlight;
    }

    35% {
      text-shadow: 0 0 35px $color-secondary-highlight;
    }

    45% {
      text-shadow: 0 0 45px $color-secondary-highlight;
    }

    55% {
      text-shadow: 0 0 55px $color-secondary-highlight;
    }

    85% {
      text-shadow: 0 0 85px $color-secondary-highlight;
    }
  }

}

.neon {
  @include neon();
}


