  right: 40px; }

.stars1:after,
.stars1:before,
.stars2:after,
.stars2:before {
  position: absolute;
  content: "";
  display: block;
  height: 0.25rem;
  width: 0.25rem;
  background: #fff0f0;
  border-radius: 50%;
  transition: 0.2s all ease; }

.stars1:after {
  top: 8px;
  right: 20px; }

.stars1:before {
  top: 18px;
  right: -12px; }

.stars2:after {
  top: -8px;
  right: -16px; }

.stars2:before {
  top: 6px;
  right: -26px; }

.sun-moon {
  z-index: 2;
  position: absolute;
  left: 0;
  display: inline-block;
  height: 2.5rem;
  width: 2.5rem;
  margin: 0.25rem;
  background: #FFFDF2;
  border-radius: 50%;
  transition: all 0.5s ease;
  /* Default to Moon */
  border: 2px solid #b18597; }

.sun-moon .dots {
  position: absolute;
  top: 9px;
  left: 20px;
  height: 1rem;
  width: 1rem;
  background: #EFEEDB;
  border: 2px solid #DEE2C6;
