.sun-moon .dots:after,
.sun-moon .dots:before {
  position: absolute;
  content: "";
  display: block;
  height: 0.25rem;
  width: 0.25rem;
  background: #EFEEDB;
  border: 2px solid #DEE2C6;
  border-radius: 50%;
  transition: 0.4s all ease; }

.sun-moon .dots:after {
  top: 0px;
  left: -20px; }

.sun-moon .dots:before {
  top: 20px;
  left: -10px; }

/* Transition to Sun */
.switch input:checked ~ .sun-moon {
  left: calc(100% - 3rem);
  background: #ffd972;
  border-color: #E7C65C;
  transform: rotate(-25deg); }

.switch input:checked ~ .sun-moon .dots,
.switch input:checked ~ .sun-moon .dots:after,
.switch input:checked ~ .sun-moon .dots:before {
  background: #FFFFFF;
  border-color: #FFFFFF; }

